* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  font-family: "Montserrat", sans-serif;
}
body {
  background-color: rgb(255, 245, 111);
  padding: 0px 16px;
}

.keeper-head {
  background-color: #f5ba13;
  margin: auto -17px;
  padding:23px 23px ;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

.keeper-head h3 {
  color: #fff;
  font-family: "McLaren", cursive;
  font-weight: 200;
}
.note_Box {
  background: #fff;
  position: relative;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 35px 30px;
  width:300px;
  word-wrap: break-word;
  float: left;
  margin: 10px 20px;
  text-align: justify;
}
.note-Box h1 {
  font-size: 1.1em;
  margin-bottom: 16px;
  padding: 52px 12px;
}
.note_Box p {
  font-size: 1.1em;
  white-space: pre-wrap;
  word-wrap: break-word;
}
